<template>
  <div id="about">
    <b-header :produce="produce"></b-header>
    <b-count :produce="produce"></b-count>
  </div>
</template>
<script>
import BHeader from './a-header'
import BCount from './ACount'
import { getProduce } from '../api'
const id = window.location.href.split('?')[1].split('=')[1]
export default {
  name: 'Home',
  data () {
    return {
      produce: {}
    }
  },
  created () {
    getProduce({ id }).then((data) => {
      this.produce = data
    })
  },
  components: {
    BHeader,
    BCount
  }
}
</script>
